.layout__header .region-header {
  height: 90px;
  display: grid;
  align-items: center;
  grid-template-columns: 130px auto; }
  .layout__header .region-header .site-branding {
    margin-right: 30px; }
  .layout__header .region-header .header__nav ul.menu {
    display: flex;
    align-items: center;
    list-style: none; }
    .layout__header .region-header .header__nav ul.menu li.menu__item {
      margin-right: 15px; }
      .layout__header .region-header .header__nav ul.menu li.menu__item a {
        color: #333333;
        font-size: 16px;
        font-weight: 600; }
      .layout__header .region-header .header__nav ul.menu li.menu__item:last-child {
        margin-right: 0; }

.layout__nav {
  background-color: #24313d;
  border-bottom: 8px solid #f54c0b; }
  .layout__nav .menu-content ul.menu {
    list-style: none;
    display: flex;
    align-items: center; }
    .layout__nav .menu-content ul.menu li.menu__item {
      border-right: 1px solid #1c252e; }
      .layout__nav .menu-content ul.menu li.menu__item a {
        display: flex;
        height: 60px;
        align-items: center;
        color: #FFFFFF;
        font-size: 14px;
        font-weight: bold;
        transition: all 0.2s ease-in-out;
        padding: 0 23px; }
        .layout__nav .menu-content ul.menu li.menu__item a:hover {
          background-color: #f54c0b; }
      .layout__nav .menu-content ul.menu li.menu__item:first-child a {
        text-indent: -9999999px;
        background: url("./img/home-icon.svg") no-repeat center;
        width: 70px;
        padding: 0; }
        .layout__nav .menu-content ul.menu li.menu__item:first-child a:hover {
          background: url("./img/home-icon.svg") no-repeat center, #f54c0b; }

.blog .view__content .views-row {
  margin-bottom: 30px;
  border-bottom: #EBF2F6 1px solid;
  position: relative;
  padding-bottom: 30px; }
  .blog .view__content .views-row h3 {
    margin-bottom: 10px; }
    .blog .view__content .views-row h3 a {
      color: #4A4A4A;
      transition: color 0.3s ease;
      font-size: 22px;
      font-weight: 600;
      line-height: 32px; }
      .blog .view__content .views-row h3 a:hover {
        color: #111; }
  .blog .view__content .views-row .field-summary {
    margin-bottom: 15px; }
    .blog .view__content .views-row .field-summary p {
      line-height: 20px; }
  .blog .view__content .views-row .views-row__footer {
    display: flex;
    justify-content: space-between; }
    .blog .view__content .views-row .views-row__footer .field-tags a {
      color: #9EABB3; }
    .blog .view__content .views-row .views-row__footer .node-post-date {
      color: #9EABB3; }
  .blog .view__content .views-row:last-child {
    margin-bottom: 0; }
  .blog .view__content .views-row:after {
    display: block;
    content: "";
    width: 7px;
    height: 7px;
    border: #E7EEF2 1px solid;
    position: absolute;
    bottom: -5px;
    left: 50%;
    margin-left: -5px;
    background: #FFF;
    border-radius: 100%;
    box-shadow: #FFF 0 0 0 5px; }

a {
