body {
  box-sizing: border-box;
  font-family: "Lato", -apple-system, BlinkMacSystemFont, "Segoe UI", Arial, sans-serif;
  font-size: 14px;
  margin: 0;
  background-color: #fff; }

a {
  color: #2e5bff; }

*, *::before, *::after {
  box-sizing: inherit; }

b, strong {
  font-family: "Lato", -apple-system, BlinkMacSystemFont, "Segoe UI", Arial, sans-serif;
  font-weight: 700; }

address, cite, dfn, em, i {
  font-family: "Lato", -apple-system, BlinkMacSystemFont, "Segoe UI", Arial, sans-serif;
  font-style: italic; }

pre, code, kbd, samp {
  font-family: Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace; }

blockquote, dl, dd, figure, p, pre {
  margin: 0; }

h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
  margin: 0; }

pre, code, kbd, samp {
  font-family: monospace; }

summary {
  outline: none;
  cursor: pointer; }

ol, ul {
  margin: 0;
  padding: 0; }

img {
  max-width: 100%; }

iframe {
  border: 0; }

label {
  cursor: pointer;
  display: block; }

button {
  user-select: none;
  cursor: pointer;
  padding: 0;
  background-color: transparent;
  border: 0;
  font-family: "Lato", -apple-system, BlinkMacSystemFont, "Segoe UI", Arial, sans-serif;
  font-size: 14px; }
  button:focus {
    outline: none; }

select {
  cursor: pointer; }

input, optgroup, select, textarea {
  width: 100%;
  border: 0; }
  input:focus, optgroup:focus, select:focus, textarea:focus {
    outline: none; }

textarea {
  resize: none;
  margin: 0;
  font-family: "Lato", -apple-system, BlinkMacSystemFont, "Segoe UI", Arial, sans-serif; }

input[type="checkbox"],
input[type="radio"] {
  padding: 0;
  margin: 0; }
